import React, { useState } from "react"
import SearchForm from "../SearchForm"
import SearchResults from "../SearchResults"
import data from "../../algorithm/disciplinas.json"
import "./style.css"

/**
* Match any substring (partial) from a string (text).
*/
function matches (text: string, partial: string) {
    return text.toLowerCase().indexOf(partial.toLowerCase()) > -1
}

export function decode(str: string) { 
    const txt = document.createElement("textarea")
    txt.innerHTML = str
    return txt.value
}

function format(course: {
    code: string,
    name: string,
    pre: Array<{code: string, name: string}>,
    co: Array<{code: string, name: string}>,
    equiv: Array<{code: string, name: string}
>}) {
    return `${course.code} : ${course.name}`
}

function SearchArea({setDiscipline} : {setDiscipline: React.Dispatch<React.SetStateAction<string>>}) {
    const [results, setResults] = useState<string[]>([]);

    const handleSearch = (param1: string, param2: string, useParam1: boolean, useParam2: boolean) => {
        let newResults = Array<string>()

        for (const item of data)
        {
            if (param1)
            {
                if (item.department === Number.parseInt(param1))
                {
                    for (const course of item.classes)
                    {
                        const label = decode(format(course))
                        if (!param2 || matches(label, param2))
                            newResults.push(label)
                    }
                    break;
                }
            }

            else if (param2)
                for (const course of item.classes)
                {
                    const label = decode(format(course))
                    if (matches(label, param2))
                        newResults.push(label)
                }
            else
            {
                newResults = newResults.concat(item.classes.map(course => decode(format(course))))
            }
        }

        setResults(newResults);
    };

    return (
        <div className="searchArea">
            <SearchForm onSearch={handleSearch} />
            <SearchResults setDiscipline={setDiscipline} results={results} />
            <span>
                Copyleft   2024 | Feito com   por <a href="https://github.com/R-enanVieira">Renan V. Guedes</a> e <a href="https://github.com/beyondmagic">Joo V. Farias</a>
            </span>
        </div>
    );
}

export default SearchArea;
