import { TopologicalSort } from "../../algorithm/topological_sort";
import React, { useCallback, useEffect } from 'react';
import {
    ReactFlow,
    Controls,
    useNodesState,
    useEdgesState,
    addEdge,
    //useReactFlow,
} from '@xyflow/react';
import CustomNode from '../CustomNode';
import { decode } from '../SearchArea/index';

import '@xyflow/react/dist/style.css';
import './style.css';

const initalNodes = [
];

const initialEdges = [];

const nodeTypes = {
    custom: CustomNode,
};

async function loadGraphData (setEdges: any, setNodes: any, discipline: string) {
    const {classes, edges: edgesData} = TopologicalSort(discipline);
    //console.log(edgesData)
    const initialNodes = classes.map((course, index) => ({
        id: course.code,
        position: {x: 100 * index, y: 50 * index}, 
        data: {code: course.code, name: decode(course.name)},
        type: 'custom',
    }));

    setNodes(initialNodes);

    const initialEdges = edgesData.map(({from, to}) => ({
        id: `${from}-${to}`,
        source: from,
        target: to,
    }));
    
    setEdges(initialEdges);
}

function GraphCanvas({discipline} : {discipline: string}) {
    const [nodes, setNodes, onNodesChanges] = useNodesState(initalNodes);
    const [edges, setEdges, onEdgesChanges] = useEdgesState(initialEdges);
    //const { fitView } = useReactFlow();

    
    // useEffect(() => {
    //     fitView({padding: 0.2});
    // }, [fitView, nodes]);

    useEffect(() => {
        loadGraphData(setEdges, setNodes, discipline);
    }, [setNodes, setEdges, discipline]);

    const onConnect = useCallback(
        (params : any) => setEdges((eds) => 
        addEdge(params, eds)),
        [setEdges],
    );

    return (
        <div className='graph'>
            <ReactFlow
                nodes={nodes}
                edges={edges}
                onNodesChange={onNodesChanges}
                onEdgesChange={onEdgesChanges}
                onConnect={onConnect}
                nodeTypes={nodeTypes}
            >
                <Controls />
            </ReactFlow>

        </div>
    );
}

export default GraphCanvas;
