import React from "react";
import "./style.css";

type SearchResultsProps = {
    results: string[]
    setDiscipline: React.Dispatch<React.SetStateAction<string>>
}

function SearchResults({ results, setDiscipline }: SearchResultsProps) {
    return (
        <div className="searchResults">
            <h2>Resultados da busca</h2>
            <ul>
                {results.length > 0 ? (
                    results.map((result,index) => <li onClick={() => setDiscipline(result.split(' ')[0])} key={result}>{result}</li>)
                ) : (
                    <p>Nenhum resultado encontrado</p>
                )}
            </ul>
        </div>
    );
}

export default SearchResults;
