function SearchForm( { onSearch }: SearchFormProps ) {
    const [selectedValue, setSelectedValue] = useState('');
    const [inputLabel, setInputLabel] = useState('');
    const [param1, setParam1] = useState("");
    const [param2, setParam2] = useState("");
    const [useParam1, setUseParam1] = useState(false);
    const [useParam2, setUseParam2] = useState(false);

    const handleSearch = () => {
        onSearch(param1, useParam2 ? param2 : "", useParam1, useParam2);
    }
    
    const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const inputValue = e.target.value;
        setInputLabel(inputValue);
    
        // Procurar pelo valor correspondente na lista de opes
        const option = options.find((opt) => opt.label === inputValue);
        if (option) {
          setSelectedValue(option.value);  // Definir o valor real que ser enviado
        } else {
          setSelectedValue(''); // Limpar valor se no houver correspondncia
        }
    };

    return (
        <div className="searchForm">
            <h2>Preencha as informaes do componente que deseja consultar</h2>
            <div>
                <input
                    type="checkbox"
                    checked={useParam1}
                    onChange={() => setUseParam1(!useParam1)}    
                />
                <input
                    list="subjects"
                    id="subject-search"
                    name="subject-search"
                    placeholder="Selecione uma disciplina"
                    value={inputLabel}
                    onChange={handleInputChange}
                    disabled={!useParam1}
                />
                <datalist id="subjects">
                    {options.map((option) => (
                    <option key={option.value} value={option.label} />
                    ))}
                </datalist>
            </div>
            <div>
                <input
                    type="checkbox"
                    checked={useParam2}
                    onChange={() => setUseParam2(!useParam2) }
                />
                <input
                    type="text"
                    placeholder="Escreva o nome/cdigo do Componente"
                    value={param2}
                    onChange={e => setParam2(e.target.value)}
                    disabled={!useParam2}
                />
            </div>
            <button type="button" onClick={handleSearch}>Buscar</button>
        </div>
    );
}   

export default SearchForm;
